/* http://meyerweb.com/eric/tools/css/reset/ 
v2.0 | 20110126
License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.contact-sec {
  width: 100%;
  height: 590px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.consec-box {
  width: 80%;
  height: 400px;
  border: solid 2px black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px; }

.con-form {
  display: flex;
  width: 70%;
  justify-content: center;
  margin: 10px 0 10px 0; }

.con-form p {
  font-family: serif; }

.con-left {
  width: 15%; }

.form {
  width: 100%; }

.con-right {
  width: 50%;
  margin-left: 50px; }

.consub {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 30%; }

@media screen and (max-width: 800px) {
  .consec-box {
    width: 90%;
    height: 400px;
    border: solid 2px black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px; }
  .con-left {
    width: 50%; }
  .quest {
    width: 100%; } }

footer {
  width: 100%;
  height: 230px;
  background-color: lightgray; }

.footer-top {
  width: 100%;
  height: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.ft-left {
  width: 27%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.ftl-box {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }

.ftl-box h1 {
  font-size: 20px;
  font-family: serif;
  font-weight: bold;
  margin-bottom: 20px;
  text-decoration: underline red; }

.ftl-box p {
  font-family: serif;
  margin: 5px 0 5px 0; }

.ftl-box p:hover {
  cursor: pointer;
  color: red; }

.ftl-box a {
  text-decoration: none; }

.ftl-box a:hover {
  color: red; }

.ftl-box p:visited {
  color: black; }

.ft-middle {
  width: 10%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.ft-forth {
  width: 23%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.ft-right {
  width: 10%;
  height: 155px;
  display: flex;
  flex-direction: column;
  align-items: center; }

.scroll {
  width: 5%;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: flex-end; }

.scroll:hover {
  color: red; }

.footer-bottom {
  width: 100%;
  height: 30px;
  text-align: center;
  font-family: serif;
  border-top: solid 2px white;
  display: flex;
  align-items: center;
  justify-content: center; }

header {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: rgba(0, 0, 10, 0);
  position: fixed; }

header a {
  text-decoration: none; }

header h1 {
  color: red;
  font-size: 40px;
  font-weight: bold;
  font-family: 'Times New Roman', Times, serif; }

header p {
  color: gray;
  font-family: serif;
  font-size: 15px; }

section {
  width: 100%; }

.section-pic {
  width: 100%;
  height: 850px; }

.anime {
  width: 7.5%;
  height: 253px;
  transition: 3s;
  display: flex;
  justify-content: center;
  align-items: center; }

.anime:hover {
  background-color: white; }

.anime p {
  color: #990303; }

main {
  display: flex;
  border-top: solid 2px black;
  border-bottom: solid 2px black;
  margin-top: 50px;
  margin-bottom: 50px;
  padding-top: 50px;
  padding-bottom: 50px; }

aside {
  width: 10%;
  text-align: center; }

ul {
  position: sticky;
  top: 0px; }

li {
  padding: 20px 0 20px 0; }

a:visited {
  color: #00000a; }

.fa-twitter:hover {
  color: #05ecec; }

.fa-facebook-square:hover {
  color: #0d40e7; }

.fa-instagram:hover {
  color: purple; }

.fa-line:hover {
  color: #13c213; }

.fa-snapchat-ghost:hover {
  color: #fafa0f; }

article {
  width: 90%;
  border-left: solid 2px black;
  display: flex;
  flex-direction: column;
  align-items: center; }

.article-content {
  width: 90%;
  height: 400px;
  background-color: #00000a;
  border-bottom: solid 1px white;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.article-pic {
  width: 30%;
  height: 250px;
  overflow: hidden; }

.article-pic img {
  display: block;
  transition-duration: 0.3s; }

.article-pic img:hover {
  transform: scale(1.1);
  transition-duration: 0.3s;
  opacity: 0.6; }

.article-p {
  width: 50%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }

.article-p h1 {
  font-family: serif;
  font-size: 40px;
  font-weight: bold;
  color: white;
  border-bottom: solid 5px red; }

.article-p p {
  color: white;
  font-family: serif;
  line-height: 20px; }

.fade_off {
  opacity: 0;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  -ms-transition: all 1s;
  transition: all 1s; }

.fade_on {
  opacity: 1; }

@media screen and (max-width: 1439px) {
  .anime {
    display: none; } }

@media screen and (max-width: 800px) {
  aside {
    display: none; }
  article {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-left: none; }
  .article-content {
    width: 90%;
    height: 550px;
    background-color: #00000a;
    border-bottom: solid 1px white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; }
  .article-pic {
    width: 70%;
    height: 250px;
    overflow: hidden; }
  .article-p {
    width: 90%;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-around; } }
